import { useLocation } from "@reach/router"
import { graphql, Link, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import { useIntl } from "gatsby-plugin-intl"
import React from "react"
import { Button, Dropdown, Nav, NavItem, NavLink } from "react-bootstrap"
import { FaBars, FaHome, FaInfoCircle, FaMobileAlt } from "react-icons/fa"
import SearchWrapper from "./search/searchWrapper"
import Ad from "./ad"

export const query = graphql`
  query {
    file(relativePath: { eq: "gatsby-icon.png" }) {
      childImageSharp {
        # Specify the image processing specifications right in the query.
        # Makes it trivial to update as your page's design changes.
        fixed(width: 35, height: 35) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`

const Topbar = ({ handleToggleSidebar }) => {
  // Making useIntl available in the code
  const intl = useIntl()
  // Use language iso for the routes
  const locale = intl.locale !== "en" ? `/${intl.locale}` : ""

  const data = useStaticQuery(query)
  const { pathname } = useLocation()
  return (
    <>
      <div
        className="btn-toggle"
        onClick={() => handleToggleSidebar(true)}
        aria-hidden="true"
      >
        <FaBars size={28} />
      </div>
      <div className="topbar">
        {/* 1.leftbar */}
        <div className="branding my-auto pt-1 pb-2">
          <Link to="/">
            <div className="site-logo d-flex align-items-center">
              <Img fixed={data.file.childImageSharp.fixed} />
            </div>
          </Link>
          <h6 className="text-white pl-2">
            {intl.formatMessage({ id: `siteMetaData.tagLine.${process.env.GATSBY_DISTRICT}` })}
          </h6>
        </div>
        <SearchWrapper />
        {/* 2.navbar */}
        <div className="topbar-nav pt-1">
          <Nav
            className="justify-content-center"
            defaultActiveKey="/home"
            as="ul"
          >
            <Nav.Item as="li">
              <Link className="nav-link" to={`${locale}/`}>
                <div>
                  <FaHome />
                </div>
                <span className={pathname === `${locale}/` ? "active" : ""}>
                  {intl.formatMessage({ id: "home" })}
                </span>
              </Link>
            </Nav.Item>
            <Nav.Item as="li">
              <Link className="nav-link" to={`${locale}/about-us`}>
                <div>
                  <FaInfoCircle />
                </div>
                <span
                  className={pathname === `${locale}/about-us` ? "active" : ""}
                >
                  {intl.formatMessage({ id: "about-us" })}
                </span>
              </Link>
            </Nav.Item>
            <Nav.Item as="li">
              <Link className="nav-link" to={`${locale}/contact-us`}>
                <div>
                  <FaMobileAlt />
                </div>
                <span
                  className={
                    pathname === `${locale}/contact-us` ? "active" : ""
                  }
                >
                  {intl.formatMessage({ id: "contact-us" })}
                </span>
              </Link>
            </Nav.Item>
          </Nav>
        </div>
      </div>
      <div className="d-flex flex-wrap justify-content-around align-items-center mt-2 mb-3">
        <div className="p-1 mb-2 text-center text-white bg-warning">
              01641622541 / 01521500642   
          contact@rajdoctors.com
        </div>
        <div className="d-flex justify-content-center">
          <Button
            as="a"
            href="https://forms.gle/GKAX2UrKYpcoYyTB7"
            target="_blank"
            variant="outline-primary"
            size="sm"
            className="mr-2 mb-xs-2"
          >
            Add a New Doctor
          </Button>
          <Button
            as="a"
            href="https://forms.gle/5gqnndwoBUV2vSef7"
            target="_blank"
            variant="outline-primary"
            size="sm"
          >
            Add a New Telemedicine Doctor
          </Button>
          {/* <Button as='a' href='https://forms.gle/kW2NFG9X34suvKXN6' target='_blank' variant='outline-info mb-2'>Ask For an Appointment</Button> */}
        </div>
        {/* <Dropdown as={NavItem} style={{ width: "10rem" }}>
          <Dropdown.Toggle as={NavLink} className="nav-link">
            {intl.locale === "en" ? " " : "Change to English"}
          </Dropdown.Toggle>
          <Dropdown.Menu>
            {intl.locale !== "en" && (
              <Dropdown.Item>
                <Link className="nav-link" to={`/`}>
                  <span className="bg-light text-dark">English</span>
                </Link>
              </Dropdown.Item>
            )}
            {intl.locale === "en" && (
              <Dropdown.Item>
                <Link className="nav-link" to={`/bn/`}>
                  <span className="bg-light text-dark"></span>
                </Link>
              </Dropdown.Item>
            )}
          </Dropdown.Menu>
        </Dropdown> */}
      </div>
      <div className="px-1">
        {(pathname !== `${locale}/about-us` &&  pathname !== `${locale}/contact-us`) && <Ad isHorizontal={true} />}
      </div>
    </>
  )
}

export default Topbar
